:root {
  --header-height: 60px;
  --main-color: #3498db;
  --border-color: #00acdc;
}

body {
  margin: 0;
}

.logo {
  align-items: center;
  width: 40px;
  height: 40px;
  padding: 0 50px;
  display: flex;
}

.header {
  height: var(--header-height);
  background: #fff;
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 2px 4px #0000000d;
}

.nav {
  margin: 0 50px;
}

.nav_list {
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style: none;
  display: flex;
}

.nav_item:not(:first-child) {
  margin-left: 20px;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  padding-top: 22px;
  padding-bottom: 22px;
  font-family: Roboto, sans-serif;
  line-height: 16px;
  text-decoration: none;
  display: inline-block;
  position: relative;
}

.nav_link:hover {
  color: var(--border-color);
}

.is-active:after {
  content: "";
  background: var(--border-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: var(--border-color);
  position: relative;
}

